import {Api_url} from '@/common/config'
export default {
	data() {
		return {  
			login:false
		};
	}, 
	props:[
		'auth'
	],  
	watch:{
		'auth.is_name'(news){  
			this.check_userAuth()
		},
		'auth.is_address'(news){
			console.log('address:',news)
		},
		'auth.is_phone'(news){
			console.log('phone:',news)
		}
	},
	methods: {
		check_userAuth() {
			console.log('授权开始')
			const that = this;
			uni.getSetting({
				success(data) {
					console.log('获取已授权列表', data)
					if (data.authSetting['scope.userInfo']) {
						console.log('已授权过了') 
						uni.getUserInfo({
							success: data => {  
								let user=data.userInfo 
								that.UpUser(user,data.encryptedData,data.iv)
							}
						})
					} else {
						console.log('弹出授权框')
						that.login = true
					}
				}
			})
		},
		updateUserInfo(res) { 
			this.login = false
			const that = this;
			if (res.detail.userInfo) {
				const user = res.detail.userInfo		 
				that.UpUser(user,res.detail.encryptedData,res.detail.iv) 
			}
		},
		UpUser(user,keys,iv){				
			console.log('授权',keys)
			const that = this
			uni.request({
			  	url: Api_url+'auth/upinfo',
			  	method: 'POST',  
				data:{
					nickname: user.nickName,
					headpic: user.avatarUrl,
					keys,
					iv
				},
				header: {
					token:uni.getStorageSync("token")
				},
			  	success: function (res) {
					that.$emit('userinfo',user)
				},
			}); 
		}
	}
}
